import React, { useState } from 'react'
import { Link } from "react-router-dom";
import SliderComponent from './SliderComponent';
import SubTitle from './SubTitle';
import EventList from './EventList';
import EventListCard from './EventListCard';
import MenuCard from './MenuCard';
import InstagramCard from './InstagramCard';
import { Parallax } from 'react-parallax';

import '../../style/scss/mainpage.scss'


function Contents() {
    
    //menu
    const [display, setDisplay] = useState([false, false, false]);
    function outMouseDisplay() {
        setDisplay([false, false, false]);
    }
    function inMouseDisplay(index) {
        let newDisplay = [...display];
        newDisplay[index] = true;
        setDisplay(newDisplay);
    }

    //subTitle props
    const titleProps1 = {
        title: 'EVENT LIST',
        type: 'Sweet pleasure',
    }
    const titleProps2 = {
        title: <>TODAY'S<br/>CHOCOLATE</>,
        type: 'Chocolate with love',
    }

    //event props
    const eventProps1 = {
        cardName: display[1] ? 'hover' : '',
        img: <img src={ require('../../assets/img/main/event1.png') } alt="2020   " />,
        back: <>2020   <br />Spring Enchanteur Collection</>,
    }
    const eventProps2 = {
        cardName: display[2] ? 'hover' : '',
        img: <img src={ require('../../assets/img/main/event2.png') } alt="   " />,
        back: <>   <br />GODIVA MAMBERSHIP</>,
    }
    const eventProps3 = {
        cardName: display[3] ? 'hover' : '',
        img: <img src={ require('../../assets/img/main/event3.png') } alt="  " />,
        back: <>  <br />Premium Cup Ice Cream</>,
    }

    const eventList = {
        listName1: 
            <li 
                onMouseEnter={() => inMouseDisplay(1)}
                onMouseLeave={() => outMouseDisplay()}
            >
                2020   <br />Spring Enchanteur Collection 
            </li>,
        listName2: 
            <li 
                onMouseEnter={() => inMouseDisplay(2)}
                onMouseLeave={() => outMouseDisplay()}
            >
                   <br />GODIVA MAMBERSHIP
            </li>,
        listName3: 
            <li 
                onMouseEnter={() => inMouseDisplay(3)}
                onMouseLeave={() => outMouseDisplay()}
            >
                  <br />Premium Cup Ice Cream
            </li>,
    }

    //godiva menu props
    const menuProps1 = {
        img: <img src={ require('../../assets/img/main/menu1.jpg') } alt=" " />,
        name: ' ', 
        value: '      ',
    }
    const menuProps2 = {
        img: <img src={ require('../../assets/img/main/menu2.jpg') } alt=" " />,
        name: ' ', 
        value: '     ',
    }
    const menuProps3 = {
        img: <img src={ require('../../assets/img/main/menu3.jpg') } alt=""  className='desktop'/>,
        mImg: <img src={ require('../../assets/img/main/m_menu3.jpg') } alt=""  className='mobile'/>,
        name: '', 
        value: '   ,     ',
    }
    const menuProps4 = {
        img: <img src={ require('../../assets/img/main/menu4.jpg') } alt="" className='desktop'/>,
        mImg: <img src={ require('../../assets/img/main/m_menu4.jpg') } alt=""  className='mobile'/>,
        name: '', 
        value: '    ',
    }
    const menuProps5 = {
        img: <img src={ require('../../assets/img/main/menu5.jpg') } alt="" className='desktop'/>,
        mImg: <img src={ require('../../assets/img/main/m_menu5.jpg') } alt="" className='mobile'/>,
        name: '', 
        value: '     ',
    }

    //instagram props
    const instaProps1 = {
        link: "https://www.instagram.com/godivakorea/?hl=ko",
        img: <img src={ require('../../assets/img/main/insta1.jpg') } alt="" />,
        type: '# # # # # # #  # # #godiva #godivachocolate #WonderAwaits',
        mType: '#',
    }
    const instaProps2 = {
        link: "https://www.instagram.com/godivakorea/?hl=ko",
        img: <img src={ require('../../assets/img/main/insta2.jpg') } alt="" />,
        type: '# # # # # # #  # # #godiva #godivachocolate #WonderAwaits',
        mType: '#',
    }
    const instaProps3 = {
        link: "https://www.instagram.com/godivakorea/?hl=ko",
        img: <img src={ require('../../assets/img/main/insta3.jpg') } alt="" />,
        type: '# # # #godiva #godivachocolate',
        mType: '#',
    }
    const instaProps4 = {
        link: "https://www.instagram.com/godivakorea/?hl=ko",
        img: <img src={ require('../../assets/img/main/insta4.jpg') } alt="" />,
        type: '#godiva Our Signature Coffee Blend is made from perfectly roasted Arabica beans, sourced from thr finest growing regions in the world. Treat yourself to its exceptional, full-bodied flavor at any GODIVA Cafe.',
        mType: '#godivacafe',
    }
    const instaProps5 = {
        link: "https://www.instagram.com/godivakorea/?hl=ko",
        img: <img src={ require('../../assets/img/main/insta5.jpg') } alt="" />,
        type: '       50  !    50              .',
        mType: '#GODIVA50',
    }
    const instaProps6 = {
        link: "https://www.instagram.com/godivakorea/?hl=ko",
        img: <img src={ require('../../assets/img/main/insta6.jpg') } alt="" />,
        type: '@godiva NEW GODIVA Cube Truffles. Four luxurious layers of flavor. Exquisite inside  out.',
        mType: '#CubeTruffles',
    }
    const instaProps7 = {
        link: "https://www.instagram.com/godivakorea/?hl=ko",
        img: <img src={ require('../../assets/img/main/insta7.jpg') } alt="" />,
        type: '@godiva The best way to celebrate #NationalStrawberryDay ? By covering them in GODIVA chocolate. Check out our stories for a special! treat!',
        mType: '#StrawberryDay',
    }

    //email
    const [hover, setHover] = useState(false);
    const emailMouseOver = () => {
        setHover(true);
    }
    const emailMouseOut = () => {
        setHover(false);
    }


    return (
        <>
            <SliderComponent />

            {/*   */}
            <Parallax 
                className="chocoBg" 
                strength={500} 
                bgImage={require('../../assets/img/main/choco_bg2.png')}
            >

                {/* event */}
                <div className="event">
                    <div className="container">
                        <div className="left">
                            <SubTitle {...titleProps1}/>
                            <EventList {...eventList}/>
                        </div>
                        <div className="right">
                            <div className="cardGroup">
                                <div className="card logo">
                                    <div className="wrap logo">
                                        <img src={ require('../../assets/img/main/event_imegeLogo.png') } alt=" " /> 
                                    </div>
                                </div>
                                <EventListCard {...eventProps1}/>
                                <EventListCard {...eventProps2}/>
                                <EventListCard {...eventProps3}/>
                                <EventList {...eventList}/>
                            </div>
                        </div>            
                    </div>
                </div>

                {/* today's chocolate */}
                <div className="today">
                    <div className="container">
                        <div className="left">
                            <SubTitle {...titleProps2}/>
                        </div>
                        <input type="button" className="go" value="" />
                        <div className="right">
                            <div className="choco"><img src={ require('../../assets/img/main/chocolate-01.png') } alt="Tourbillon " /></div>
                            <div className="contents">
                                <div className="name">Tourbillon </div>
                                <div className="ex"> 85%     .             .</div>
                                <input type="button" className="btnMore" value="MORE" />
                            </div>   
                        </div>
                    </div>
                </div>

                {/* godiva menu */}
                <div className="gMenu">
                    <div className="container">
                        <div className="box">
                            <MenuCard {...menuProps1}/>
                            <MenuCard {...menuProps2}/>
                            <Link to="/">
                            <div className="boxover menuTitle">
                                <span>G</span>
                                <span>O</span>
                                <span>D</span>
                                <span>I</span>
                                <span>V</span>
                                <span>A</span>
                                <span> </span>
                                <span>M</span>
                                <span>E</span>
                                <span>N</span>
                                <span>U</span>
                            </div>
                            </Link>
                            <MenuCard {...menuProps3}/>
                            <MenuCard {...menuProps4}/>
                            <MenuCard {...menuProps5}/>
                        </div>
                    </div>
                </div>
            </Parallax>

            {/* */}
            <div className="store">
                <Link to="/">
                    <div className="container">
                        <div className="iceImg"><img src={ require('../../assets/img/main/ice.png') } alt="" /></div>
                        <div className="text">
                            <div className="title"><span>    </span> </div>
                            <div className="type">Sweet Place</div>
                        </div>
                        <input type="button" value="" />
                    </div>
                </Link>
            </div>

            {/*  */}
            <div className="insta">
                <div className="instaGroup">
                    <InstagramCard {...instaProps1}/>
                    <InstagramCard {...instaProps2}/>
                    <div className="box colorBox">
                        <div>SHOW US<br />HOW YOU<br />SHARE<br />THE LOVE<br />#GODIVA</div>
                    </div>
                    <InstagramCard {...instaProps3}/>
                    <InstagramCard {...instaProps4}/>
                    <InstagramCard {...instaProps5}/>
                    <InstagramCard {...instaProps6}/>
                    <InstagramCard {...instaProps7}/>
                    <div className="box colorBox">
                        <div>#GODIVA<br />#LOVESTAGRAM<br /></div>
                    </div>
                </div>
            </div>

            {/* email */}
            <div className="email">
                <div className="container">
                    <input placeholder="Type to your e-mail adress" />
                    <div className="sign">
                        <input type="submit" value="sign" onMouseEnter={emailMouseOver} onMouseLeave={emailMouseOut}/>
                        <div><span className={`subMenu ${hover ? 'mouseOver' : 'mouseOut'}`}></span></div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Contents