import React from 'react'
import { Link } from "react-router-dom";
import SubVisual from './SubVisual';
import SubBanner from './SubBanner';
import ContentsBox from './ContentsBox';
import '../../style/scss/aboutgodiva.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { faCircleArrowRight } from '@fortawesome/free-solid-svg-icons';

function AboutGodiva() {

    //subVisual props
    const aboutSubVisual = {
        logo: <p>GODIVA</p>,
        title: 'ABOUT GODIVA',
        type: 'A special gift for loved ones',
        subGroup: 
        <ul className="subGroup">
        <Link to="#"><li> </li></Link>
        <Link to="#"><li> </li></Link>
        </ul>,
    }

    //subBanner props
    const aboutBanner = {
        class: "subBanner off",
        bannerText: "  ",
        icon: <FontAwesomeIcon icon={faCircleArrowRight} className="icon"/>,
    }

    //contentBox props
    const ContentBox1 = {
        class: 'list story',
        title: ' ',
        type: 'Our Godiva',
        ex: '   ',
        btn: ' ',
    }
    const ContentBox2 = {
        class: 'list shop',
        title: ' ',
        type: 'Our Stores',
        ex: <> 1  <br />  37   </>,
        btn: ' ',
    }

    return (
        <div className='aboutGodiva'>
            {/* visual */}
            <SubVisual {...aboutSubVisual}/>
            {/* banner */}
            <div className='miniBanner'>
                <SubBanner {...aboutBanner} />
            </div>
            {/* list */}
            <div className='menuList'>
                <ContentsBox {...ContentBox1}/>
                <ContentsBox {...ContentBox2}/>
            </div>
        </div>
    )
}

export default AboutGodiva