import React from 'react'
import { Link } from "react-router-dom";
import SubVisual from './SubVisual';
import SubBanner from './SubBanner';
import ContentsBox from './ContentsBox';
import '../../style/scss/godivamenu.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { faCircleArrowRight } from '@fortawesome/free-solid-svg-icons';

function GodivaMenu() {

  //subVisual props
  const godivaMenuSubVisual = {
    logo: <p>GODIVA</p>,
    title: 'MENU',
    type: 'Premium chocolate for you',
    subGroup: 
    <ul className="subGroup">
      <Link to="#"><li></li></Link>
      <Link to="#"><li></li></Link>
      <Link to="#"><li></li></Link>
      <Link to="#"><li></li></Link>
    </ul>,
  }

  //subBanner props
  const doubleBanner1 = {
    class: "subBanner double hot",
    bannerText: "What's HOT",
    icon: <FontAwesomeIcon icon={faCircleArrowRight} className="icon"/>,
  }
  const doubleBanner2 = {
    class: "subBanner double today",
    bannerText: <>MD<br />  </>,
    img: <img src={ require('../../assets/img/sub_menu/choco.png') } alt="" />,
    icon: <FontAwesomeIcon icon={faCircleArrowRight} className="icon"/>,
  }

  //contentBox props
  const ContentBox1 = {
    class: 'list choco',
    title: '',
    type: 'Chocolate',
    ex: '   ',
    btn: '',
  }
  const ContentBox2 = {
    class: 'list drink',
    title: '',
    type: 'Drink',
    ex: <>    <br />  ,  </>,
    btn: '',
  }
  const ContentBox3 = {
    class: 'list ice',
    title: '',
    type: 'IceCream',
    ex: <>   <br />    </>,
    btn: '',
  }
  const ContentBox4 = {
    class: 'list snack',
    title: '',
    type: 'SnackDessert',
    ex: '      ',
    btn: '',
  }

  return (
    <div className='godivaMenu'>
      {/* visual */}
      <SubVisual {...godivaMenuSubVisual}/>
      {/* banner */}
      <div className='miniBanner'>
        <SubBanner {...doubleBanner1} />
        <SubBanner {...doubleBanner2} />
      </div>
      {/* list */}
      <div className='menuList'>
        <ContentsBox {...ContentBox1}/>
        <ContentsBox {...ContentBox2}/>
        <ContentsBox {...ContentBox3}/>
        <ContentsBox {...ContentBox4}/>
      </div>
    </div>
  )
}

export default GodivaMenu