import React from 'react'
import "../../style/scss/slick.scss";
import "../../style/scss/slick-theme.scss";
import "../../style/scss/slider.scss"
import Slider from "react-slick";

function SliderCompont() {
  
  const settings = {
    dots: true,
    infinite: true,
    speed: 300,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay : true,
    autoplaySpeed : 3000, 	
  };

  const settingsRegular = {
    dots: false,
    infinite: true,
    speed: 300,
    arrows: false,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay : true,
    autoplaySpeed : 3000, 	
    vertical: true,
  };

  return (
    <>
      <Slider {...settings} className="mainSlider">
        <div><img src={ require('../../assets/img/main/event_slide1.jpg') } alt="1" /></div>
        <div><img src={ require('../../assets/img/main/event_slide2_2.jpg') } alt="2" /></div>
        <div><img src={ require('../../assets/img/main/event_slide3.jpg') } alt="3" /></div>
      </Slider>

      <Slider {...settings} className="mobileSlider">
        <div><img src={ require('../../assets/img/main/m_event1.jpg') } alt="1" /></div>
        <div><img src={ require('../../assets/img/main/m_event2.jpg') } alt="2" /></div>
        <div><img src={ require('../../assets/img/main/m_event3.jpg') } alt="3" /></div>
      </Slider>

      <div className="regularEvent">
        <div className="container">
            <div className="title"><span>Regular</span><br /><span>events</span></div>
            <Slider {...settingsRegular} className="regularSlider">
              <ul className="rEvent">
                  <li><span># </span>#</li> 
                  <li># <span>#</span></li>
              </ul>
              <ul className="rEvent">
                  <li># <span>#5000</span></li>
                  <li># <span>#1000</span> #</li>
              </ul>
              <ul className="rEvent">
                  <li><span>#3 </span> # #</li> 
                  <li># # <span>#</span></li>
              </ul>
            </Slider>
        </div>
        <Slider {...settingsRegular} className="mContainer">
          <li><span># </span> #</li> 
          <li># <span> #</span></li>
          <li># <span> #5000</span></li>
          <li># <span> #1000</span> #</li>
          <li><span>#3 </span> # #</li> 
          <li># # <span> #</span></li>
        </Slider>
      </div>
    </>
  )
}

export default SliderCompont